          .col-lg
            p Un informe contable campesino es un documento sencillo donde se registra todo lo que ocurre con el dinero en la finca o unidad productiva: lo que entra (ingresos) y lo que sale (costos y gastos). Es comparable al control de la economa familiar, pero enfocado en la actividad agrcola. En palabras simples, es como un espejo que refleja la situacin econmica de la finca.

