      h2 5.2 Qu se necesita para hacer un informe contable o balance de la economa campesina?

    p No se necesita ser contador ni tener una computadora para llevar un informe contable. Con un cuaderno, un lpiz y organizacin, cualquier campesino puede construir su propio registro econmico de manera sencilla y til. A continuacin, se presentan los pasos fundamentales para elaborar un informe contable campesino:


    AcordionA(tipo="a" clase-tarjeta="tarjeta bg-24")
      .row(titulo="Paso 1.  Anotar todos los ingresos")

        p.mb-0.px-5 Registrar cada entrada de dinero, sea por ventas o apoyos, es el primer paso. Es importante incluir siempre:
        p.mb-0.px-5 Fecha
        p.mb-0.px-5 Concepto (qu se vendi o de dnde proviene el dinero)
        p.mb-0.px-5 Monto recibido

